<template>
  <div>
    <p>title</p>
    <input type="text" v-model="title">
    <p>neirong</p>
    <input type="text" v-model="concent">
    <div class="btn" @click="add()">tianjia</div>
  </div>
</template>

<script>
import store from '@/store/index'
export default {
    name: 'Add',
    store,
    data () {
        return {
            title: '',
            concent: ''
        }
    },
    methods: {
        add () {
            store.commit('addItem', {
                title: this.title,
                concent: this.concent
            }),
            this.title = '',
            this.concent = '',
            this.$router.push('/home/list')
        }  
    }
}
</script>

