<template>
    <div>
        <form v-if="!isReg">
            <div>user: </div>
            <input type="text" v-model="name">
            <div>pass: </div>
            <input type="password" v-model="password">
            <div>
                <button type="button" @click="login()">login</button>
                <button type="button" @click="reg()">info</button>
            </div>
            
            
        </form>
        <form v-else>
            <div>user: </div>
            <input type="text" v-model="name">
            <div>pass: </div>
            <input type="password" v-model="password">
            <div>pass again: </div>
            <input type="password" v-model="repate">
            <div>
                <button type="button" @click="addUser()">sure</button>
                <button type="button" @click="cancel()">cancel</button>
            </div>
        </form>
    </div>
</template>

<script>
export default {
    name: 'Login',
    data () {
        return {
            isReg: false,
            name: '',
            password: '',
            repate: ''
        }
    },
    methods: {
        login () {
            if (localStorage.getItem("name") === this.name && localStorage.getItem("password") === this.password){
                this.name = '',
                this.password = '',
                this.$router.push('/home/list')
            }else {
                alert('username or password no equels!')
            }  
        },
        reg () {
            this.isReg = true
        },
        addUser (){
            if (this.password ===  this.repate){
                localStorage.setItem("name", this.name),
                localStorage.setItem("password", this.password)
                this.name = '',
                this.password = '',
                this.isReg = false
            }else {
                alert('Twice pass no equels!')
            }
        },
        cancel () {
            this.isReg = false
        }
    }
}
</script>

